namespace $ {
	export class $hyoo_draw_colors extends $mol_switch {
		
		/**
		 * ```tree
		 * Option* $hyoo_draw_colors_option
		 * 	color <= color_raw*
		 * 	checked?val <=> option_checked*?val
		 * 	enabled <= option_enabled*
		 * 	minimal_height 40
		 * ```
		 */
		@ $mol_mem_key
		Option(id: any) {
			const obj = new this.$.$hyoo_draw_colors_option()
			
			obj.color = () => this.color_raw(id)
			obj.checked = (val?: any) => this.option_checked(id, val)
			obj.enabled = () => this.option_enabled(id)
			obj.minimal_height = () => 40
			
			return obj
		}
		
		/**
		 * ```tree
		 * keys /
		 * 	\red
		 * 	\pink
		 * 	\purple
		 * 	\indigo
		 * 	\blue
		 * 	\cyan
		 * 	\teal
		 * 	\green
		 * 	\yellow
		 * 	\orange
		 * 	\brown
		 * 	\coral
		 * 	\blue_grey
		 * 	\grey
		 * 	\negative
		 * ```
		 */
		keys() {
			return [
				"red",
				"pink",
				"purple",
				"indigo",
				"blue",
				"cyan",
				"teal",
				"green",
				"yellow",
				"orange",
				"brown",
				"coral",
				"blue_grey",
				"grey",
				"negative"
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * color_raw* \
		 * ```
		 */
		color_raw(id: any) {
			return ""
		}
	}
	
	export class $hyoo_draw_colors_option extends $mol_check {
		
		/**
		 * ```tree
		 * style *
		 * 	^
		 * 	backgroundColor <= color
		 * ```
		 */
		style() {
			return {
				...super.style(),
				backgroundColor: this.color()
			}
		}
		
		/**
		 * ```tree
		 * color \
		 * ```
		 */
		color() {
			return ""
		}
	}
	
}

